<template>
  <article>
    <div class="container">
      <h2>JACS</h2>
      <p class="catch">10<br class="visible-xs"></p>
      <p>JACS<br>
      <br class="hidden-sm">
      </p>
    </div>
  </article>
</template>

<script>
export default {
  name: 'ActivityBlock',
};
</script>

<style lang="scss" scoped>
  @import "../assets/stylesheets/_mixin";

  article {
    margin: 60px 0;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 170px;
      margin-top: 2.5rem;
      background: #ededed url("../assets/images/bg_activity.jpg") left 50% bottom 50% no-repeat;
      background-size: cover;
      @include media( sm ) {
        height: 250px;
      }
      @include media( md ) {
        height: 350px;
      }
      @include media( lg ) {
        background-size: contain;
      }
    }
  }
  .container {
    @include container;
  }
  h2 {
    @include title;
  }
  p {
    text-align: left;
    @include media( sm ) {
      text-align: center;
    }
  }
  .catch {
    @include catch;
    text-align: center;
  }
</style>



